body {
  background: #fff;
  font-family: "SF Compact Display", sans-serif;
}

.container {
  padding: 0 16px;
  margin: 0 auto;
  max-width: 1312px;
  box-sizing: border-box;
  width: 100%;
}

.title {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.55556;
  color: #ffb81c;
}
.title + .title__text {
  margin: 16px 0 0;
}
.title__text {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 600;
  font-size: 36px;
  line-height: 1.22222;
  letter-spacing: -0.02em;
  color: #1f2a37;
}
.title__text span {
  color: #9da4ae;
}
.title__subtitle {
  margin: 12px 0 0;
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #6c737f;
}
.title__subtitle p {
  font-family: "SF Compact Display", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #6c737f;
  margin: 12px 0 0;
}

.nav {
  padding: 15px 0;
}
.nav__logo {
  max-width: 200px;
}
.nav__logo img {
  width: 100%;
}
.nav .container {
  display: flex;
  justify-self: start;
  align-items: center;
  gap: 50px;
}
.nav p {
  font-family: "SF Compact Display", sans-serif;
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
}

.header__info {
  position: absolute;
  position: absolute;
  bottom: 32px;
  left: 32px;
  right: 32px;
  z-index: 5;
  max-width: 800px;
}
.header__info h1,
.header__info p {
  color: #fff;
  text-shadow: #000 0px 0px 10px;
  font-size: 60px;
}
.header__wrapper {
  margin: 50px 0 0;
  border-radius: 40px;
  position: relative;
  overflow: hidden;
  align-items: stretch;
}
.header__slide {
  border-radius: 40px;
  position: relative;
  overflow: hidden;
  height: auto;
}
.header__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-height: 720px;
}

.about {
  padding: 50px 0 0;
}
.about__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 32px;
  margin: 30px 0 0;
}
.about__item {
  padding: 24px;
  border-radius: 24px;
  border: 2px solid #e5e7eb;
  display: grid;
  grid-template-rows: auto 1fr;
  gap: 32px;
}
.about__item p {
  font-size: 18px;
  color: #1f2a37;
  line-height: 1.5;
  font-weight: 600;
}
.about__item p + p {
  border-radius: 24px;
  padding: 32px 24px;
  background: #11111f;
  font-size: 20px;
  font-weight: 700;
  color: #fff;
}

.catalog {
  padding: 50px 0 0;
}
.catalog__title p {
  margin: 15px 0 0;
}
.catalog__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px;
  margin: 30px 0 0;
}
.catalog__wrapper span {
  display: block;
  border-radius: 22px;
  position: relative;
  overflow: hidden;
  height: 200px;
  background: rgba(0, 0, 0, 0.2);
}
.catalog__wrapper img {
  width: 100%;
  height: 100%;
  min-height: 100%;
  object-fit: cover;
  transition: all 0.3s;
}
.catalog__wrapper p {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  color: #11111f;
  padding: 20px 12px 0;
}
.catalog__wrapper a:hover img {
  transform: scale(1.1);
}
.catalog__info {
  margin: 35px 0 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  color: #11111f;
  padding: 20px 12px 0;
}

.content {
  padding: 50px 0 25px;
}
.content__wrapper {
  margin: 30px 0 0;
  position: relative;
  overflow: hidden;
  align-items: stretch;
}
.content__slide {
  border-radius: 40px;
  position: relative;
  overflow: hidden;
  height: auto;
}
.content__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-height: 720px;
}
.content .swiper-pagination {
  position: relative !important;
  inset: auto !important;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100% !important;
  transform: none !important;
  margin: 20px 0 0;
  gap: 5px;
}
.content .swiper-pagination span {
  width: 8px;
  height: 8px;
  display: block;
  background: #0F70B7;
  margin: 0 !important;
  border-radius: 100%;
  transform: none !important;
}

@media (max-width: 1200px) {
  .about__wrapper {
    grid-template-columns: 1fr 1fr;
  }
  .catalog__wrapper {
    grid-template-columns: 1fr 1fr;
  }
  .content__slide img,
  .header__slide img {
    height: 56.25vw;
  }
}
@media (max-width: 768px) {
  .title__text {
    font-size: 24px;
  }
  .header__info h1 {
    font-size: 32px;
  }
  .about__wrapper {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 600px) {
  .catalog__wrapper {
    grid-template-columns: 1fr;
  }
}